import React, { useState, useEffect } from 'react';
import axios from 'axios';
import API_BASE_URL from '../../components/apiConfig';

const ServiceSection = () => {
    const [services, setServices] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await axios.get(`${API_BASE_URL}/service/upload/`);
                setServices(response.data);
            } catch (error) {
                console.error('Error fetching data:', error);
                setServices([]);
            }
        };

        fetchData();
    }, []);

    return (
        <section id="services" className="services">
            <div className="container">
                <div className="section-title">
                    <h2>Huduma Zetu Za Afya</h2>
                    <p>"NIHD, tumejitolea kwa ustawi wako, tumejitolea kukuza ulimwengu ambapo afya hukutana na furaha. Kwa shauku ya kukuza ustawi na kuwezesha uchaguzi wa afya, tunajivunia kuwa mshirika wako unayemwamini katika safari ya afya bora.</p>
                </div>
                <div className="row">
                    {services.map((services, index) => (
                        <div
                            key={index}
                            className="col-lg-4 col-md-6 d-flex align-items-stretch"
                        >
                            <div className="icon-box">
                                <div className="icon">
                                    <img src={services.image} style={{width:"50px"}} alt="service-img" />
                                    
                                </div>
                                <h4>{services.service}</h4>
                             
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default ServiceSection;
