import React, { useState, useEffect } from 'react';
import logomin from '../../UserAssets/img/logomin2.png';
import StrategicareaModal from './StrategicareaModal';
import { Link } from 'react-router-dom';



const Header = () => {
  const logoStyle = {
    width: '75px',
    height: '100px',
    marginBottom: '16px',
  };

  const [mobileMenuVisible, setMobileMenuVisible] = useState(false);

  const toggleMobileMenu = () => {
    setMobileMenuVisible(!mobileMenuVisible);
  };

  const closeMobileMenu = () => {
    setMobileMenuVisible(false);
  };

  const scrollto = (el) => {
    let header = document.querySelector('#header');
    let targetElement = document.querySelector(el);

    if (targetElement) {
      let offset = header.offsetHeight;
      let elementPos = targetElement.offsetTop;
      window.scrollTo({
        top: elementPos - offset,
        behavior: 'smooth',
      });
    }
  };

  useEffect(() => {
    setMobileMenuVisible(false);
  }, []);

  return (
    <div>
     
    

<header id="header" className="fixed-top">

      <div className="container d-flex align-items-center">
        <img src={logomin} alt="Company Logo" style={logoStyle} />

        <div className="logo me-auto" style={{ marginLeft: '5px', paddingRight: '40px' }}>
          <h1 className="logo me-auto">
            <a href="index.html">NIHD</a>
          </h1>
          <p style={{ fontSize: '5pt', color: '#2c4964' }}>NEHEMIAH II INTERMOLECULAR HEALTH DRUGS</p>
        </div>

        <nav
          id="navbar"
          className={`navbar order-last order-lg-0 ${mobileMenuVisible ? 'navbar-mobile' : ''}`}
        >
          <ul>
            <li>
              <a
                className="nav-navLink scrollto active"
                href="#hero"
                onClick={() => {
                  scrollto('#hero');
                  closeMobileMenu();
                }}
              >
                Nyumbani
              </a>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#about"
                onClick={() => {
                  scrollto('#about');
                  closeMobileMenu();
                }}
              >
                Kuhusu
              </a>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#services"
                onClick={() => {
                  scrollto('#services');
                  closeMobileMenu();
                }}
              >
                Huduma
              </a>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#products"
                onClick={() => {
                  scrollto('#departments');
                  closeMobileMenu();
                }}
              >
                Bidhaa zetu
              </a>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#management"
                onClick={() => {
                  scrollto('#management');
                  closeMobileMenu();
                }}
              >
               Usimamizi
              </a>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#gallery"
                onClick={() => {
                  scrollto('#gallery');
                  closeMobileMenu();
                }}
              >
                Picha
              </a>
            </li>
            <li className="dropdown">
              <Link to="strategic">
                <span  data-bs-toggle="modal" data-bs-target="#strategicmodal">
                <span>Huduma za Kimikakati</span>
                </span>
              </Link>
              <ul>
            
              </ul>
            </li>
            <li>
              <a
                className="nav-navLink scrollto"
                href="#contact"
                onClick={() => {
                  scrollto('#contact');
                  closeMobileMenu();
                }}
              >
                Mawasiliano
              </a>
            </li>
          </ul>
          <i
            className={`bi bi-list mobile-nav-toggle ${mobileMenuVisible ? 'bi-x' : ''}`}
            onClick={toggleMobileMenu}
          ></i>
        </nav>
        <a href="#products" className="appointment-btn scrollto" style={{ marginBottom: '25px' }}>
          <span className="d-none d-md-inline">Tazama</span> Bidhaa
        </a>
        


      </div>

     
    </header>
   

    </div>
    
  );
};

export default Header;
