import React, { useEffect, useState } from 'react';
import axios from 'axios';
import API_BASE_URL from '../../components/apiConfig';


const About = () => {
  const [backgroundImage, setBackgroundImage] = useState('');
  const [storyDescription, setStoryDescription] = useState('');
  const [aboutDescription, setAboutDescription] = useState('');
  const [iconBoxData, setIconBoxData] = useState([]); // State to store icon box data
  const [healthValues, setHealthValues] = useState([]);

  useEffect(() => {
    // Fetch background image
    axios.get(`${API_BASE_URL}/background/upload/`)
      .then(response => {
        setBackgroundImage(response.data[0].image);
      })
      .catch(error => {
        console.error('Error fetching background image:', error);
      });

    // Fetch story description
    axios.get(`${API_BASE_URL}/aboutone/upload/`)
      .then(response => {
        setAboutDescription(response.data.description);
        setStoryDescription(response.data[0].story);
      })
      .catch(error => {
        console.error('Error fetching story description:', error);
      });

    // Fetch icon box data from your database API
    axios.get(`${API_BASE_URL}/abouttwo/upload/`)
      .then(response => {
        setIconBoxData(response.data);
      })
      .catch(error => {
        console.error('Error fetching icon box data:', error);
      });

    // Fetch health values
    axios.get(`${API_BASE_URL}/health/upload/`)
      .then(response => {
        setHealthValues(response.data);
      })
      .catch(error => {
        console.error('Error fetching health values:', error);
      });
  }, []);

  const mystyle = {
    backgroundImage: `url(${backgroundImage})`,
    backgroundPosition: 'center center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    minHeight: '600px',
    height: '700px',
  };
  
  return (
    <>
      <section id="about" className="about">
        <div className="container-fluid">
          <div className="section-title">
            <h2>Tujue Sisi</h2>
          </div>

          <div className="row">
            <div className="col-xl-5 col-lg-6 video-box d-flex justify-content-center align-items-stretch position-relative" style={mystyle}>
              <a href="https://youtu.be/RnEbi5n2jYc?si=zlgNnX7qQEWM2bjJ" className="glightbox play-btn mb-4"></a>
            </div>

            <div className="col-xl-7 col-lg-6 icon-boxes d-flex flex-column align-items-stretch justify-content-center py-5 px-lg-5">
              <h3>Kuhusu Sisi</h3>
              <p>{aboutDescription}</p>

              <button className="btn btn-primary w-25" style={{ backgroundColor: '#87b3ed' }} type="button" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
                Historia yetu
              </button>

              <div className="collapse" id="collapseExample">
                <div className="card card-body">
                  {storyDescription}
                </div>
              </div>

              {iconBoxData.map((item, index) => (
                <div className="icon-box" key={index}>
                  <div className="icon"><i> <img src={item.icon} alt=""/> </i></div>
                  <h4 className="title">{item.heading}</h4>
                  <p className="description">{item.description}</p>
                </div>
              ))}

              <ul className="list-group list-group-flush" style={{ marginLeft: '70px' }}>
                {healthValues.map((value, index) => (
                
                    <span key={index}><svg width="64px" height="64px" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" stroke="#184dc9"><g id="SVGRepo_bgCarrier" strokeWidth="0"></g><g id="SVGRepo_tracerCarrier" strokeLinecap="round" strokeLinejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="M4 12H20M20 12L16 8M20 12L16 16" stroke="#1148ee" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"></path> </g></svg>{value.item}</span>
                 
                ))}
              </ul>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default About;
